import SkillBlock from "./skill-block";

const Skills = () => {
  const frontendLanguages = [
    { name: "JavaScript", skill: 8 },
    { name: "HTML", skill: 7 },
    { name: "CSS", skill: 6 },
    { name: "Bootstrap", skill: 7 },
    { name: "React JS", skill: 5 },
    { name: "JQuery", skill: 8 },
    { name: "Ajax", skill: 8 },
  ];
  const backendLanguages = [
    { name: "Java", skill: 9 },
    { name: "J2EE", skill: 8 },
    { name: "Servlet / JSP", skill: 9 },
    { name: "Spring web mvc", skill: 6 },
    { name: "Spring boot", skill: 8 },
    { name: "Node JS", skill: 3 },
    { name: "Hibernate", skill: 6 },
    { name: "Android", skill: 7 },
    { name: "H2 database", skill: 8 },
    { name: "MySQL", skill: 8 },
    { name: "MS SQL server", skill: 7 },
    { name: "Stored Procedures", skill: 4 },
  ];

  const tools = [
    { name: "Apache Netbeans", skill: 9 },
    { name: "Eclipse", skill: 6 },
    { name: "Spring Tool Suite", skill: 6 },
    { name: "VS code", skill: 6 },
    { name: "Php MyAdmin", skill: 8 },
    { name: "SQL Yog", skill: 8 },
    { name: "SQL server", skill: 5 },
    { name: "Postman", skill: 7 },
    { name: "Notepad ++", skill: 8 },
  ];

  return (
    <section
      className="resume-section p-3 p-lg-5 d-flex flex-column"
      id="skills"
    >
      <div className="my-auto">
        <h1 className="display-3 mb-5 text-dark">
          Skills
          <span className="small fs-4 text-muted"></span>
        </h1>

        <SkillBlock
          title="Frontend Technologies"
          dataList={frontendLanguages}
        />
        <SkillBlock title="Backend Technologies" dataList={backendLanguages} />
        <SkillBlock title="Tools &amp; Utilities" dataList={tools} />
      </div>
    </section>
  );
};

export default Skills;
